import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, TextField, TablePagination } from '@material-ui/core';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  search: {
    marginBottom: 10,
  },
});

const DatabaseList = () => {
  const classes = useStyles();

  // Retrieve listDatabase from local storage
  const data = JSON.parse(localStorage.getItem("data"));
  const listDatabase = data?.data?.listDatabase || [];

  // Pagination state
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  // Search state
  const [searchTerm, setSearchTerm] = useState('');

  // Handle search input change
  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
    setPage(0);
  };

  // Filter the list based on search term
  const filteredList = listDatabase.filter((row) => {
    return row.DatabaseName.toLowerCase().includes(searchTerm.toLowerCase());
  });

  // Determine the rows to display based on pagination state
  const displayList = filteredList.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  // Handle pagination change
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <TableContainer component={Paper}>
      <TextField
        label="Search Database Name"
        value={searchTerm}
        onChange={handleSearchChange}
        className={classes.search}
        fullWidth
      />
      <Table className={classes.table} aria-label="Database List">
        <TableHead>
          <TableRow>
            <TableCell>Database Name</TableCell>
            <TableCell>Description</TableCell>
            <TableCell align="right">DB Id</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {displayList.map((row) => (
            <TableRow key={row.DBId}>
              <TableCell component="th" scope="row">
                {row.DatabaseName}
              </TableCell>
              <TableCell>{row.Description}</TableCell>
              <TableCell align="right">{row.DBId}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={filteredList.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </TableContainer>
  );
}

export default DatabaseList;
