import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { navigate, useNavigate } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh",
    marginTop: theme.spacing(5),
  },
  header: {
    textAlign: "center",
    marginBottom: theme.spacing(0),
  },
  form: {
    width: 400,
    padding: 20,
    marginTop:-40,
    backgroundColor: "#fff",
    boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.2)",
    textAlign:"center"
  },
}));

function Login() {
  const classes = useStyles();
  const navigate = useNavigate();
  const [ip, setIp] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(`Username: ${username}, Password: ${password}, IP Address: ${ip}`);
  
    const url = `http://${ip}/admin/login`; // Create URL using IP address and path
    const data = { username, password }; // Create data object
  
    // Send post request using fetch API
    fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
    .then(response => {
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      return response.json();
    })
    .then(data => {
      console.log(data);
      localStorage.setItem("data", JSON.stringify(data));
      navigate("/DatabaseGUI/databaselist")
    })
    .catch(error => {
      console.error('There was an error!', error);
    });
  };
  

  return (
    <div className={classes.root}>
      <form className={classes.form} onSubmit={handleSubmit}>
      <h1 className={classes.header}>Login</h1>
      <TextField
          id="ip"
          label="IP address"
          value={ip}
          onChange={(event) => setIp(event.target.value)}
          fullWidth
          type="tel"
          margin="normal"
        />
       <TextField
          id="username"
          label="User name"
          value={username}
          onChange={(event) => setUsername(event.target.value)}
          fullWidth
          margin="normal"
        />
        <TextField
          id="password"
          label="Password"
          type="password"
          value={password}
          onChange={(event) => setPassword(event.target.value)}
          fullWidth
          margin="normal"
        />
        <Button variant="contained" color="primary" type="submit" >
          Login
        </Button>
      </form>
    </div>
  );
}

export default Login;
